
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import cPanel from './Panel'
import ContentNav from './ContentNav'

export default {
  components: {
    Swiper,
    SwiperItem,
    Scroller,
    LoadMore,
    cPanel,
    ContentNav
  },
  computed: {
    data() {
      this.$nextTick(() => {
        this.status.pullupStatus = 'default'
        this.$refs.scroller.reset()
      })
      return this.$store.state.base_data
    }
  },
  methods: {
    pullup() {
      this.$store.dispatch('getListBy', ++this.page)
    }
  },
  data () {
    return {
      page: 1,
      status: {
        pullupStatus: 'default'
      }
    }
  }
}



